@media only screen and (max-width: 600px) {
    .project {
        height:-webkit-fit-content;
        height:-moz-fit-content;
        height:fit-content;
    }
    .project-image{ 
        height: auto;
        max-width: 100%;
    }
    .description{
        margin:30px;
    }
    }
    .container {
        position: relative;
        --width: 50%;
      }
      
      .image {
        opacity: 1;
        display: block;
        --width: 100%;
        height: 300px;
        ;
        transition: .5s ease;
        -webkit-backface-visibility: hidden;
                backface-visibility: hidden;
      }
      
      .middle {
        transition: .5s ease;
        opacity: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        text-align: center;
      }
      
      .container:hover .image {
        opacity: 0.3;
      }
      
      .container:hover .middle {
        opacity: 1;
      }
      
      .text {
        background-color: #343633;
        border-radius: 7px;
        color: white;
        font-size: 16px;
        padding: 16px 32px;
      }
      .bottom-buttons{
          display:flex;
          flex-direction: column !important;
          align-items: center;
      }
      .live-button{
          width:300px;
          color:#343633 !important;
          border-color: #343633 !important;
          --margin-top:10px !important;
          border-radius: 1px !important;
          height:42px;
          margin-top:-0.990px;
      }

      .svg_icons{
        -webkit-transform: scale(1.8);
                transform: scale(1.8);
      }


      .live-button:hover{
        background-color:#343633 !important;
        opacity: 0.8;
        background-color: inherit;
        color:#E4DFDA !important;
      }
      
      .icon-bottom{
          font-size:10px;
      }
      .github-icon{height:22px !important;}
      .github-icon, .MuiSvgIcon-root {
          padding-right: 10px !important;
      }
     
      .github-button{
          background-color: inherit;
          width: 300px;
          color:#a08c8c;
          border-radius: 1px !important;
          height:42px;
          border-color: #343633 !important;
          margin-bottom: 5px !important;
      }
      .github-button:hover{
        --border-color: white;
        background-color:#343633 !important;
        /*#343633 !important;*/   
        opacity: 0.8;
        color:#E4DFDA !important;
    }
      @media only screen and (max-width: 750px) {
       .project{flex-direction: column !important;}
