
.project-image{
    height: 300px;
}
.column-project{
    display: flex;
    flex-direction: column;
    background-color: #E4DFDA;
    border-radius: 3px;
    margin:4px;
    padding:18px;
}
.project{
    display: flex;
    flex-direction: row;
    width: 100%;
    height:auto;
    --background-color:#F08A4B;
    
    list-style-type: none;
    
    color:#343633;
    
    --justify-content: space-evenly;
    align-items: center;
}
.picture-holder{
    padding-bottom:100%;
    --background:pink;
    height:0;
    position:relative;
}
p{
    font-size: 14px;
}

.column h3{
    text-align: center;
}
.column p{
    text-align: left;
}
.left{text-align: left;
}

.images img{
    --max-width: 100%;
    max-height: 100%;
    --display: block;
    --padding-left:15px;  
}
.description{
    margin:30px;
}
.project:hover{
    --opacity: 0.8;
}

.title{
    color:#285172;
    text-align: left !important;
    margin-left:30px;
}
.tech{ 
    margin-bottom:0px;
    margin-top:10px;
    margin-left:30px;
    text-align: left !important;
}    


.project-link Button{
    text-align: left !important;
    margin-top: 15px !important;
    color:#343633 !important;
    border-color:#343633 !important;
}
a{
    text-decoration: none;
    color: #343633;
}
.paragraph{
    margin-top:5px !important;
}
@media only screen and (max-width: 600px) {
    .project {
        height:fit-content;
    }
    .project-image{ 
        height: auto;
        max-width: 100%;
    }
    .description{
        margin:30px;
    }
    }
    .container {
        position: relative;
        --width: 50%;
      }
      
      .image {
        opacity: 1;
        display: block;
        --width: 100%;
        height: 300px;
        ;
        transition: .5s ease;
        backface-visibility: hidden;
      }
      
      .middle {
        transition: .5s ease;
        opacity: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        text-align: center;
      }
      
      .container:hover .image {
        opacity: 0.3;
      }
      
      .container:hover .middle {
        opacity: 1;
      }
      
      .text {
        background-color: #343633;
        border-radius: 7px;
        color: white;
        font-size: 16px;
        padding: 16px 32px;
      }
      .bottom-buttons{
          display:flex;
          flex-direction: column !important;
          align-items: center;
      }
      .live-button{
          width:300px;
          color:#343633 !important;
          border-color: #343633 !important;
          --margin-top:10px !important;
          border-radius: 1px !important;
          height:42px;
          margin-top:-0.990px;
      }

      .svg_icons{
        transform: scale(1.8);
      }


      .live-button:hover{
        background-color:#343633 !important;
        opacity: 0.8;
        background-color: inherit;
        color:#E4DFDA !important;
      }
      
      .icon-bottom{
          font-size:10px;
      }
      .github-icon{height:22px !important;}
      .github-icon, .MuiSvgIcon-root {
          padding-right: 10px !important;
      }
     
      .github-button{
          background-color: inherit;
          width: 300px;
          color:#a08c8c;
          border-radius: 1px !important;
          height:42px;
          border-color: #343633 !important;
          margin-bottom: 5px !important;
      }
      .github-button:hover{
        --border-color: white;
        background-color:#343633 !important;
        /*#343633 !important;*/   
        opacity: 0.8;
        color:#E4DFDA !important;
    }
      @media only screen and (max-width: 750px) {
       .project{flex-direction: column !important;}
        .column-project{
            width: 100%;
            margin-right: 0;
            padding:0;
        }
        .bottom-buttons{margin-bottom:20px;}
        .github-icon{
            height:22px !important;
        }
        .center-horisontal{
            width: 80%;
            height: auto;
            margin-top:15px;
         }
        }